import {
	mapState
} from 'vuex';
export default {
	props: ['isFixed', 'titArr', 'listData', 'colorKey', 'load', 'titShow', 'listActive', 'fixedTop', 'inverted'],
	data() {
		return {
			paddingHeight: 0,
			sortType: 1, //当前那个模块的排序按钮出现
			upSort: true,//是否是升序
		};
	},
	computed: {
		loadText() {
			if (this.load == '1') {
				return '加载更多';
			} else if (this.load == '2') {
				return '正在加载...';
			} else if (this.load == '3') {
				return '没有更多了';
			} else {
				return '';
			}
		}
	},
	mounted() {
		console.log('我加载了');
		if (this.fixedType == 1) {
			console.log(this.sysInfo)
			this.fixedTop = this.sysInfo.rheight + 'px';
		}
		this.getTitHeight();
	},
	methods: {
		itemsClick(data) {
			console.log('???', data)
			this.$emit('itemClick', data)
		},
		changeList(type) {
			this.$emit('changeList', type)
		},
		getTitHeight() {
			let that = this;
			const query = uni.createSelectorQuery().in(this)
			query.select('.titWrap').boundingClientRect()
			query.selectViewport().scrollOffset()
			query.exec(function(res) {
				that.paddingHeight = res[0].height + 'px';
			})
		},
		sortData(type) {
			if(type == this.sortType) {
				this.upSort = !this.upSort;
			} else {
				this.sortType = type;
				this.upSort = true;
			}
			this.$emit('sortData',type,this.upSort);
		}
	}
}
